.men { position: relative; width: 720px; background: #FFF; }

.men__border { position: absolute; top: 0; left: 0; right: 0; bottom: 0; stroke-dasharray: 910px; stroke-dashoffset: 910px; }
.men__border svg { display: block; transition: stroke-dashoffset 1.5s ease; }
.men__border.is-animate { stroke-dashoffset: 0; }

.men__table { position: relative; z-index: 1; table-layout: fixed; }

.men__cell { text-align: left; padding: 0 10px 0 45px; vertical-align: middle; box-sizing: border-box; }
.men__cell:first-child { width: 195px; }
.men__cell:nth-child(2) { width: 235px; }
.men__cell:last-child { width: 290px; }

.men__row1 .men__cell { height: 90px; }

.men__row2 .men__cell { height: 100px; }


